import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../../environments/environment';
import { Demo } from '../demo/demo.component';
import { FreeTrial } from '../../common/model/free-trial-model';

@Injectable({ providedIn: 'root' })
export class SaleAutoMationService {
  channelPartner: any;
  constructor(private client: HttpClient) {}

  getCountry(): Observable<any> {
    return this.client.get<any>(environment.apiUrl + '/countries');
  }
  setChannelPartnerById(data: any) {
    this.channelPartner = data;
  }
  getChannelPartnerData(data: any) {
    return this.channelPartner;
  }
  SaveFreeTrial(data: FreeTrial): any {
    return this.client.post<any>(environment.apiUrl + '/freeTrial', data);
  }
  SaveDemo(data: Demo): any {
    return this.client.post<any>(environment.apiUrl + '/demoForm', data);
  }
  resendFreeTrialVerification(email: string, password: string): any {
    return this.client.get<any>(
      environment.apiUrl +
        '/reSendVerificationForFreeTrial/' +
        email +
        '/' +
        password
    );
  }
  getChannelPartnerById(Id: string) {
    return this.client.get<any>(
      environment.apiUrl + '/sales/automation/channel-partner/' + Id
    );
  }
}
