import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';
import { globalVariableService } from '../common/globalService';
import { LoaderService } from '../shared/interceptor/loader.service';
import { StartupService } from '../startupService';
import { SaleAutoMationService } from './shared/sales-automation-service';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-sales-automation-home',
  templateUrl: './sales-automation-home.component.html',
  styleUrls: ['./sales-automation-home.component.scss'],
})
export class SalesAutomationHomeComponent implements OnInit {
  partnerId: any;
  title = 'sales-automation';
  isShown = true;
  isLoading: Subject<boolean> = this.loader.isLoading;
  isLoadingOnDemand: Subject<boolean> = this.loader.isLoadingOnDemand;
  constructor(
    private _globalVariableService: globalVariableService,
    private loader: LoaderService,
    private router: Router,
    private route: ActivatedRoute,
    private startup: StartupService,
    private _saleAutoMationService: SaleAutoMationService
  ) {
    this.partnerId = this.route.snapshot.paramMap.get('pId');
    if (this.partnerId)
      this._globalVariableService.setPartnerId(this.partnerId);
    else this.partnerId = this._globalVariableService.getPartnerId();
  }
  ngOnInit(): void {
    this.router.events.subscribe((routerEvent) => {
      if (routerEvent instanceof NavigationEnd) {
        // Get your url
        if (routerEvent.url.split('#').length > 1) {
          const url = routerEvent.url.split('#')[0];
          this.partnerId = url.split('/').pop();
        } else {
          this.partnerId = routerEvent.url.split('/').pop();
        }
        const result = this.startup.getIP().subscribe((data: any) => {
          let ip =  data && data['ip'];
          this.startup.getCountryName(ip).subscribe((result) => {
            let countryName = result.split(';').pop();
            this.startup.getAppInfo(countryName!).subscribe((data)=>{

            })
          });
          
        });

        this.startup
          .getChannelPartnerById(this.partnerId)
          .subscribe((result) => {
            if (result.data) {
              this._saleAutoMationService.setChannelPartnerById(result.data[0]);
            } else {
              this.router.navigate(
                ['no/sales/partner/exist/' + this.partnerId],
                { replaceUrl: true }
              );
            }
          });
      }
    });
    if (this.partnerId) {
    } else {
    }
  }
}
