import { APP_INITIALIZER, NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { SafeurlPipe } from './common/pipe/safe-url';
import {
  FaIconLibrary,
  FontAwesomeModule,
} from '@fortawesome/angular-fontawesome'; //Import here
import {
  faCheckSquare,
  faRssSquare,
  faSquare,
  faPlus,
  faMinus,
  faTrash,
} from '@fortawesome/free-solid-svg-icons';
import { ActivatedRoute } from '@angular/router';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { ToastrModule } from 'ngx-toastr';
import { CommonModule, DatePipe } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { NgxCaptchaModule } from 'ngx-captcha';
import { LoaderInterceptor } from './shared/interceptor/loader.interceptor';
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';
import { DashboardLayoutComponent } from './dashboard-layout/dashboard-layout.component';
  
@NgModule({
  declarations: [
    AppComponent,
    SafeurlPipe,
  ],
  imports: [
    FormsModule,
    CommonModule,
    BrowserModule,
    HttpClientModule,
    BrowserAnimationsModule,
    AppRoutingModule,
    ToastrModule.forRoot(),
    FontAwesomeModule,
    NgxCaptchaModule,
  ],
  providers: [DatePipe,
    { provide: HTTP_INTERCEPTORS, useClass: LoaderInterceptor, multi: true }
  ],
  bootstrap: [AppComponent],
})
export class AppModule {
  partnerId: any;
  constructor(private library: FaIconLibrary, private router: ActivatedRoute) {
    library.addIcons(
      faSquare,
      faCheckSquare,
      faRssSquare,
      faPlus,
      faMinus,
      faTrash
    );
    this.partnerId = this.router.snapshot.paramMap.get('pId');
  }
}
