import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { SalesAutomationHomeComponent } from './sales-automation-home/sales-automation-home.component';
import { MainLayoutComponent } from './main-layout/main-layout.component';
import { DashboardLayoutComponent } from './dashboard-layout/dashboard-layout.component';
 
const routes: Routes = [
  {
    path: 'automation',
    component:SalesAutomationHomeComponent,
    children: [
      {
        path: '',
        loadChildren: () =>
          import('./sales-automation-home/sales-automation.module').then((m) => m.SalesAutomationModule),
      }
    ],
  },  {
    path: 'portal',
    component:DashboardLayoutComponent,
    children: [
      {
        path: '',
        loadChildren: () =>
          import('./dashboard-layout/dashboard-layout.module').then((m) => m.DashboardLayoutModule),
      }
    ],
  },
  {
    path:'main',
    component:MainLayoutComponent,
    children:[
      {
        path:'',
        loadChildren: () =>
        import('./main-layout/main-layout.module').then((m) => m.MainLayoutModule),  
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes,{useHash: true})],
  exports: [RouterModule],
})
export class AppRoutingModule {}
