import { HttpClient } from '@angular/common/http';
import { environment } from '../environments/environment';
import { Injectable } from '@angular/core';

@Injectable({ providedIn: 'root' })
export class StartupService {

  constructor(private client: HttpClient) {}
  getChannelPartnerById(Id: string) {
    return this.client.get<any>(
      environment.apiUrl + '/sales/automation/channel-partner/' + Id
    );
  }
  
  getIP():any{
    return  this.client.get("https://api.ipify.org/?format=json")
  }

  getAppInfo(country:string){
    return this.client.get<any>(
        environment.apiUrl + '/app/info/' + country
      ); 
  }

  getCountryName(ip:any){
    return  this.client.get('https://ip2c.org/'+ip,{responseType: 'text'})
  }
}
